body, html {
  height: 100%;
  width: 100%; }

.angular-google-map-container {
  /*@extend  .col-md-12;  */
  height: 100%;
  width: 96%;
  left: 16px;
  position: absolute; }
  @media screen and (max-width: 453px) {
    .angular-google-map-container {
      width: 90%;
      left: 18px; } }
  @media screen and (min-width: 1110px) {
    .angular-google-map-container {
      width: 97%;
      left: 12px; } }

